"use client";

import React, { useState } from "react";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Settings, UserCircle } from "lucide-react";
import { Montserrat } from "next/font/google";
import { toast } from "sonner"; // Importation de sonner pour les notifications

// Configuration de la police Montserrat avec display: "swap"
const montserrat = Montserrat({
  subsets: ["latin"],
  weight: "800",
  display: "swap",
});

export default function AdminSettings() {
  const [formData, setFormData] = useState({
    firstName: "Admin",
    lastName: "Utilisateur",
    email: "admin@example.com",
    phone: "",
    address: {
      street: "",
      city: "",
      province: "",
    },
    password: "",
    confirmPassword: "",
    profileImage: null as File | null,
  });

  const [imagePreview, setImagePreview] = useState<string | null>(null);
  const [provinceFilter, setProvinceFilter] = useState(""); // État pour filtrer les provinces

  // Liste des 58 wilayas d'Algérie
  const provinces = [
    "Adrar", "Chlef", "Laghouat", "Oum El Bouaghi", "Batna", "Béjaïa", "Biskra", "Béchar", "Blida", "Bouira",
    "Tamanrasset", "Tébessa", "Tlemcen", "Tiaret", "Tizi Ouzou", "Alger", "Djelfa", "Jijel", "Sétif", "Saïda",
    "Skikda", "Sidi Bel Abbès", "Annaba", "Guelma", "Constantine", "Médéa", "Mostaganem", "M’Sila", "Mascara",
    "Ouargla", "Oran", "El Bayadh", "Illizi", "Bordj Bou Arréridj", "Boumerdès", "El Tarf", "Tindouf",
    "Tissemsilt", "El Oued", "Khenchela", "Souk Ahras", "Tipaza", "Mila", "Aïn Defla", "Naâma",
    "Aïn Témouchent", "Ghardaïa", "Relizane", "Timimoun", "Bordj Badji Mokhtar", "Ouled Djellal",
    "Béni Abbès", "In Salah", "In Guezzam", "Touggourt", "Djanet", "El M’Ghair", "El Menia"
  ];

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value, files } = e.target as HTMLInputElement;
    if (name === "profileImage" && files && files[0]) {
      const file = files[0];
      setFormData((prev) => ({ ...prev, profileImage: file }));
      setImagePreview(URL.createObjectURL(file));
    } else if (["street", "city", "province"].includes(name)) {
      setFormData((prev) => ({
        ...prev,
        address: { ...prev.address, [name]: value },
      }));
      if (name === "province") setProvinceFilter(value); // Met à jour le filtre avec la valeur saisie
    } else {
      setFormData((prev) => ({ ...prev, [name]: value }));
    }
  };

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (formData.password !== formData.confirmPassword) {
      toast.error("Les mots de passe ne correspondent pas !"); // Utilisation de toast.error pour l'erreur
      return;
    }
    // Simulation de sauvegarde réussie
    toast.success("Informations mises à jour avec succès !"); // Utilisation de toast.success
  };

  // Filtrer les provinces en fonction de la saisie
  const filteredProvinces = provinces.filter((province) =>
    province.toLowerCase().includes(provinceFilter.toLowerCase())
  );

  return (
    <div className="min-h-screen bg-gradient-to-br from-gray-50 to-gray-200 py-12 px-4 sm:px-6 lg:px-8">
      <div className="max-w-5xl mx-auto">
        {/* Titre avec icône et police Montserrat */}
        <div className="mb-10 flex flex-col gap-4">
          <div className="flex items-center gap-3">
            <Settings className="h-8 w-8 text-black" />
            <h1
              className={`text-3xl font-extrabold text-gray-900 tracking-tight ${montserrat.className}`}
              style={{ fontFamily: "'Montserrat', sans-serif" }}
            >
              Paramètres Administrateur
            </h1>
          </div>
          <p className="text-gray-600">Gérez vos informations personnelles avec style et simplicité.</p>
        </div>

        {/* Carte du formulaire */}
        <Card className="bg-white/30 backdrop-blur-lg border border-gray-200/50 rounded-xl shadow-xl">
          <CardHeader>
            <h2
              className={`text-2xl font-bold text-gray-900 ${montserrat.className}`}
              style={{ fontFamily: "'Montserrat', sans-serif" }}
            >
              Informations Personnelles
            </h2>
            <p className="text-gray-600">Mettez à jour vos coordonnées et sécurisez votre compte.</p>
          </CardHeader>
          <CardContent>
            <form onSubmit={handleSubmit} className="space-y-6">
              {/* Photo de profil */}
              <div className="flex flex-col items-center mb-6">
                <div className="relative w-32 h-32 mb-4">
                  {imagePreview ? (
                    <img
                      src={imagePreview}
                      alt="Aperçu de la photo de profil"
                      className="w-full h-full rounded-full object-cover border-4 border-gray-200 shadow-lg"
                    />
                  ) : (
                    <UserCircle className="w-full h-full text-gray-400" />
                  )}
                </div>
                <label
                  htmlFor="profileImage"
                  className="cursor-pointer bg-gray-100 text-gray-800 px-4 py-2 rounded-lg hover:bg-gray-200 transition-all duration-200 shadow-md"
                >
                  Choisir une photo
                  <input
                    type="file"
                    id="profileImage"
                    name="profileImage"
                    accept="image/*"
                    onChange={handleChange}
                    className="hidden"
                  />
                </label>
              </div>

              {/* Nom et Prénom */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label htmlFor="firstName" className="block text-sm font-medium text-gray-700 mb-1">
                    Prénom
                  </label>
                  <input
                    type="text"
                    id="firstName"
                    name="firstName"
                    value={formData.firstName}
                    onChange={handleChange}
                    className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                    placeholder="Votre prénom"
                  />
                </div>
                <div>
                  <label htmlFor="lastName" className="block text-sm font-medium text-gray-700 mb-1">
                    Nom
                  </label>
                  <input
                    type="text"
                    id="lastName"
                    name="lastName"
                    value={formData.lastName}
                    onChange={handleChange}
                    className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                    placeholder="Votre nom"
                  />
                </div>
              </div>

              {/* Email */}
              <div>
                <label htmlFor="email" className="block text-sm font-medium text-gray-700 mb-1">
                  Email
                </label>
                <input
                  type="email"
                  id="email"
                  name="email"
                  value={formData.email}
                  onChange={handleChange}
                  className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                  placeholder="votre.email@example.com"
                />
              </div>

              {/* Numéro de téléphone */}
              <div>
                <label htmlFor="phone" className="block text-sm font-medium text-gray-700 mb-1">
                  Numéro de téléphone
                </label>
                <input
                  type="tel"
                  id="phone"
                  name="phone"
                  value={formData.phone}
                  onChange={handleChange}
                  className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                  placeholder="+213 12 345 678"
                />
              </div>

              {/* Adresse */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-gray-800">Adresse</h3>
                <div>
                  <label htmlFor="street" className="block text-sm font-medium text-gray-700 mb-1">
                    Rue
                  </label>
                  <input
                    type="text"
                    id="street"
                    name="street"
                    value={formData.address.street}
                    onChange={handleChange}
                    className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                    placeholder="Nom de la rue"
                  />
                </div>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label htmlFor="city" className="block text-sm font-medium text-gray-700 mb-1">
                      Ville
                    </label>
                    <input
                      type="text"
                      id="city"
                      name="city"
                      value={formData.address.city}
                      onChange={handleChange}
                      className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                      placeholder="Ville"
                    />
                  </div>
                  <div>
                    <label htmlFor="province" className="block text-sm font-medium text-gray-700 mb-1">
                      Province
                    </label>
                    <input
                      type="text"
                      id="province"
                      name="province"
                      value={formData.address.province}
                      onChange={handleChange}
                      list="provinces-list"
                      className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                      placeholder="Saisir ou sélectionner une province"
                    />
                    <datalist id="provinces-list">
                      {filteredProvinces.map((province) => (
                        <option key={province} value={province} />
                      ))}
                    </datalist>
                  </div>
                </div>
              </div>

              {/* Sécurité (Mot de passe) */}
              <div className="space-y-4">
                <h3 className="text-lg font-semibold text-gray-800">Sécurité</h3>
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <label htmlFor="password" className="block text-sm font-medium text-gray-700 mb-1">
                      Nouveau mot de passe
                    </label>
                    <input
                      type="password"
                      id="password"
                      name="password"
                      value={formData.password}
                      onChange={handleChange}
                      className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                      placeholder="••••••••"
                    />
                  </div>
                  <div>
                    <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700 mb-1">
                      Confirmer le mot de passe
                    </label>
                    <input
                      type="password"
                      id="confirmPassword"
                      name="confirmPassword"
                      value={formData.confirmPassword}
                      onChange={handleChange}
                      className="w-full px-4 py-3 bg-white/50 border border-gray-200 rounded-lg focus:ring-2 focus:ring-black focus:border-black transition-all duration-200 text-gray-800 placeholder-gray-400"
                      placeholder="••••••••"
                    />
                  </div>
                </div>
              </div>

              {/* Bouton de soumission */}
              <div className="flex justify-end">
                <Button
                  type="submit"
                  className="bg-black text-white px-6 py-3 rounded-lg hover:bg-gray-800 transition-all duration-300 shadow-md transform hover:-translate-y-1"
                >
                  Sauvegarder les modifications
                </Button>
              </div>
            </form>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}